"use client";

import React, { useState } from "react";
import { Calendar as CalendarComponent } from "@/components/ui/calendar";
import { format } from "date-fns";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Clock, Calendar, User } from "lucide-react";

const Dashboard = ({ appointments, userType }) => {
  const [selectedDate, setSelectedDate] = useState(new Date());

  // Filter appointments for the selected date (ignoring time)
  const appointmentsForDate = appointments.filter((app) => {
    return format(new Date(app.date), "yyyy-MM-dd") === format(selectedDate, "yyyy-MM-dd");
  });

  // Total appointments and unique doctor names
  const totalAppointments = appointments.length;
  const doctorNames = [...new Set(appointments.map((app) => app.doctorName))];
  
  // Find dates with appointments to highlight in calendar
  const appointmentDates = appointments.map(app => 
    format(new Date(app.date), "yyyy-MM-dd")
  );

  return (
    <div className="p-4 md:p-8 bg-gray-50 min-h-screen">
      <h1 className="text-2xl md:text-3xl font-bold mb-6 text-gray-800">Your Healthcare Dashboard</h1>
      
      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-lg font-medium text-gray-700 flex items-center">
              <Calendar className="h-5 w-5 mr-2 text-blue-500" />
              Total Appointments
            </CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold text-blue-600">{totalAppointments}</p>
            <p className="text-sm text-gray-500 mt-1">
              {userType === "doctor" ? "scheduled with patients" : "with your healthcare providers"}
            </p>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-lg font-medium text-gray-700 flex items-center">
              <User className="h-5 w-5 mr-2 text-indigo-500" />
              {userType === "doctor" ? "Patients" : "Your Doctors"}
            </CardTitle>
          </CardHeader>
          <CardContent>
            {doctorNames.length > 0 ? (
              <div className="flex flex-wrap gap-2">
                {doctorNames.map((name, idx) => (
                  <Badge key={idx} variant="outline" className="bg-indigo-50 text-indigo-700 border-indigo-200">
                    {name}
                  </Badge>
                ))}
              </div>
            ) : (
              <p className="text-sm text-gray-500">No appointments scheduled</p>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Main Content Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
        {/* Calendar Section */}
        <Card className="lg:col-span-3">
          <CardHeader className="pb-2">
            <CardTitle>Appointment Calendar</CardTitle>
          </CardHeader>
          <CardContent>
            <CalendarComponent
              mode="single"
              selected={selectedDate}
              onSelect={setSelectedDate}
              className="rounded-md border"
              modifiers={{
                hasAppointment: (date) => 
                  appointmentDates.includes(format(date, "yyyy-MM-dd"))
              }}
              modifiersStyles={{
                hasAppointment: { 
                  backgroundColor: "#dbeafe", 
                  color: "#1e40af",
                  fontWeight: "bold"
                }
              }}
            />
          </CardContent>
        </Card>

        {/* Daily Appointments Section */}
        <Card className="lg:col-span-1">
          <CardHeader className="pb-2">
            <CardTitle className="text-lg font-medium flex items-center">
              <Clock className="h-5 w-5 mr-2 text-green-500" />
              <span>{format(selectedDate, "MMMM d, yyyy")}</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            {appointmentsForDate.length > 0 ? (
              <div className="space-y-3">
                {appointmentsForDate.map((app) => (
                  <div key={app.id} className="p-3 rounded-lg bg-white border border-gray-200 shadow-sm hover:shadow-md transition-shadow">
                    <h4 className="font-medium text-gray-800">{app.title}</h4>
                    <div className="flex items-center text-gray-500 text-sm mt-1">
                      <Clock className="h-4 w-4 mr-1" />
                      <span>{app.time}</span>
                    </div>
                    <div className="flex items-center text-gray-500 text-sm mt-1">
                      <User className="h-4 w-4 mr-1" />
                      <span>{app.doctorName}</span>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center h-40 text-center">
                <Calendar className="h-10 w-10 text-gray-300 mb-2" />
                <p className="text-gray-500">No appointments scheduled for this day</p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default Dashboard;